import { Api } from "./base/api";
// Класс LarekAPI расширяет базовый Api класс для взаимодействия с сервером
export class LarekApi extends Api {
    // Конструктор принимает адрес CDN и базовый URL для API
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn; // Сохранение ссылки на CDN
    }
    // Получение списка товаров
    getProductList() {
        return this.get('/product').then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image // Добавление полного пути к изображению
         }))));
    }
    // Получение детальной информации о товаре по ID
    getProductItem(id) {
        return this.get(`/product/${id}`).then((item) => (Object.assign(Object.assign({}, item), { image: this.cdn + item.image })));
    }
    // Оформление заказа
    orderProducts(order) {
        return this.post(`/order`, order).then((data) => data);
    }
}
